import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner, Epic, PanelHeader, Panel, Tabbar, TabbarItem, Avatar, Div, HeaderButton, List, Cell, Snackbar, Button} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Polls from 'react-polls';




class Poll extends React.Component {

  // Handling user vote
  // Increments the votes count of answer when the user votes


  render(){

    
   return(
    <Div>
     <Cell><h1>{this.props.selectedPlace.pollQuestions}</h1></Cell>
     <div onClick={this.props.getAns}  style={{"backround":"#BF24FE","width": "100%", "border-radius": "20px"}}>{this.props.selectedPlace.answer_1}</div>
     <div onClick={this.props.getAns}  style={{"backround":"#BF24FE","width": "100%", "border-radius": "20px"}}>{this.props.selectedPlace.answer_2}</div>
     <div onClick={this.props.getAns}  style={{"backround":"#BF24FE","width": "100%", "border-radius": "20px"}}>{this.props.selectedPlace.answer_3}</div>
    </Div>
	 	);
	}
}

export default Poll;